// Imports
// —————————————————————————————————

@import "base/_index";
@import "common/_index";


// Base
// —————————————————————————————————

body {
  background-color: map-get($colors, body);
}

.l-title,
.l-section {
  display: block;
  margin: auto;
  width: calc(100% - ($gutter * 8) );
}

.l-title {
  max-width: 600px;
  padding: ($gutter * 8) 0;
  text-align: center;

  h1 {
    color: rgba(map-get($colors, text), 0.8);
    font-size: 20px;
    // margin-bottom: ($gutter * 2);
    position: relative;

    //== Children
    span {
      background-color: map-get($colors, body);
      padding: 0 ($gutter * 2);
      position: relative;
      z-index: 2;
    }
    &:after {
      background-color: rgba(map-get($colors, text), 0.1);
      content: "";
      height: 1px;
      left: 0;
      position: absolute;
      right: 0;
      top: 50%;
      z-index: 1;
    }
  }
  h2 {
    color: rgba(map-get($colors, text), 0.6);
    font-size: 15px;
    line-height: 1.7em;
    margin: auto;
    max-width: 80%;
  }
}

.l-section {
  max-width: 1000px;
  padding-bottom: ($gutter * 6);
}


.l-grid__container {
  display: flex;
  flex-wrap: wrap;
  gap: $gutter;

  .l-grid__container--2 &,
  &.l-grid__container--2 .l-item { flex: 0 0 calc( (100% - ($gutter * 1)) / 2) }
  .l-grid__container--3 &,
  &.l-grid__container--3 .l-item { flex: 0 0 calc( (100% - ($gutter * 2)) / 3) }
  .l-grid__container--4 &,
  &.l-grid__container--4 .l-item { flex: 0 0 calc( (100% - ($gutter * 3)) / 4) }
  .l-grid__container--5 &,
  &.l-grid__container--5 .l-item { flex: 0 0 calc( (100% - ($gutter * 4)) / 5) }
  .l-grid__container--6 &,
  &.l-grid__container--6 .l-item { flex: 0 0 calc( (100% - ($gutter * 5)) / 6) }
  .l-grid > & + .l-grid__container { margin-top: $gutter }
}


.l-caption {
  color: rgba(map-get($colors, text), 0.8);
  line-height: 1.5em;
  margin: ($gutter * 2) auto 0;
  max-width: 800px;
  width: calc(100% - ($gutter * 8) );

  //== Children
  strong {
    font-size: 13px;
    text-transform: uppercase;
  }
  p {
    color: rgba(map-get($colors, text), 0.6);
    font-size: 15px;
  }
}

.l-item {
  display: block;
  position: relative;

  //= Children
  img {
    display: block;
    position: relative;
    width: 100%;
    z-index: 1;
  }
  & > a {
    z-index: 2;
    @include coverer();
  }
  iframe {
    background-color: #fff;
    border: none;
    display: block;
    position: relative;
    // transform: scale(1.11);
    z-index: 1;
    width: 100%;
  }
  canvas {
    height: 100% !important;
    width: 100% !important;
  }
}


.l-grid + .l-grid,
.l-item + .l-grid,
.l-grid + .l-item { margin-top: $gutter }



@media only screen and (max-width: $breakpoint-md) {
  // .l-title, .l-section { width: calc(100% - ($gutter * 5) ) }
  // .l-caption { width: calc(100% - ($gutter * 7) ) }
  .l-grid__container--split > div,
  .l-grid__container--split > .l-item { flex: 0 0 100% !important }
}
@media only screen and (max-width: $breakpoint-sm) {
  .l-title, .l-section { width: calc(100% - ($gutter * 3) ) }
  .l-caption { width: calc(100% - ($gutter * 2) ) }
}






// Animations
@include keyframes(dotRotate){
  0% { transform: rotate(0deg) }
  100% { transform: rotate(360deg) }
}
@include keyframes(sonarRotate){
  100% { transform: rotate(-360deg) }
}




