.l-pb { padding-bottom: $gutter; }
.l-pl { padding-left: $gutter; }
.l-pr { padding-right: $gutter; }
.l-pt { padding-top: $gutter; }
.l-hpa { padding: ($gutter / 2); }
.l-hpb { padding-bottom: ($gutter / 2); }
.l-hpl { padding-left: ($gutter / 2); }
.l-hpr { padding-right: ($gutter / 2); }
.l-hpt { padding-top: ($gutter / 2); }
.l-dpa { padding: ($gutter * 2); }
.l-dpb { padding-bottom: ($gutter * 2); }
.l-dpl { padding-left: ($gutter * 2); }
.l-dpr { padding-right: ($gutter * 2); }
.l-dpt { padding-top: ($gutter * 2); }
.l-np { padding: 0 }
.l-npb { padding-bottom: 0; }
.l-npl { padding-left: 0; }
.l-npr { padding-right: 0; }
.l-npt { padding-top: 0; }
.l-pa--large { padding: ($gutter * 6); }
.l-pb--large { padding-bottom: ($gutter * 6); }
