@media only screen and (max-width: $breakpoint-sm) {
  .l-ma--large { padding: ($gutter * 4); }
  .l-mb--large { padding-bottom: ($gutter * 4); }
  .l-ml--large { padding-left: ($gutter * 4); }
  .l-mr--large { padding-right: ($gutter * 4); }
  .l-mt--large { padding-top: ($gutter * 4); }
  .l-pa--large { padding: ($gutter * 4); }
  .l-pb--large { padding-bottom: ($gutter * 4); }
  .l-pl--large { padding-left: ($gutter * 4); }
  .l-pr--large { padding-right: ($gutter * 4); }
  .l-pt--large { padding-top: ($gutter * 4); }
}

//== Various
.l-br { border-radius: $border-radius }
.l-br--small { border-radius: 3px }
.l-br--bottom { border-radius: 0 0 $border-radius $border-radius }
.l-br--top { border-radius: $border-radius $border-radius 0 0 }



//== Backgrounds and borders
//== Use variables defined within common/variables.scss
.l-bkg {
  @each $key, $val in $colors {
    &--#{$key},
    &--#{$key}--hover:hover,
    &--#{$key}--active:active,
    &--#{$key}--active.active,
    &--#{$key}--active.active:hover,
    &--#{$key}--focus:focus { background-color: $val }
  }
}
.l-bdr {
  @each $key, $val in $colors {
    &--#{$key},
    &--#{$key}--hover:hover,
    &--#{$key}--active:active,
    &--#{$key}--active.active,
    &--#{$key}--active.active:hover,
    &--#{$key}--focus:focus { border-color: $val }
  }
}
.l-bdr {
  border-style: solid;
  border-width: 1px;

  //== Variants
  &.l-bdr--double { border-width: 2px }
}


//== Hide/Show responsive
.l-show--sm,
.l-show--md { display: none }
@media only screen and (max-width: $breakpoint-md) {
  .l-show--md { display: block }
  .l-hide--md { display: none }
}
@media only screen and (max-width: $breakpoint-sm) {
  .l-show--sm { display: block }
  .l-hide--sm { display: none }
}


